import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import {WelcomePage} from '../welcome/welcome';
// import { MyApp } from '../../app/app.component';

/**
 * Generated class for the TutorialPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-tutorial',
  templateUrl: 'tutorial.html',
})
export class TutorialPage {

  slides = [
    {
      title: "Bienvenido a IPMA Chile!",
      description: "La app <b>IPMA Chile</b> contiene 2 tipos de simuladores de exmenes con los conocimientos relevantes para obtener la certificacin IPMA Level D y C.",
      image: "assets/img/titulo_ipma.png",
    },
    {
      title: "Qu es la Certificacin IPMA?",
      description: "Sistema internacional de Certificacin de Competencias de un individuo en cuanto a la <b>Gestin de Proyectos</b> que cuenta con 4 niveles",
      image: "assets/img/logo.jpg",
    },
    {
      title: "Exmenes Simples",
      description: "Los Exmenes Simples corresponden a: un <b>Exmen Full</b> que consta de 20 preguntas aleatorias y un tiempo mximo para responder de 8 minutos, y un <b>Exmen Express</b> que consta de 10 preguntas aleatorias y un tiempo mximo para responder de 2 minutos",
      image: "assets/img/examenes_simples.png",// Imagen de Exmenes Full + Express
    }
    ,
    {
      title: "Exmenes Categorizados",
      description: "La Norma ICB es el estndar internacional del IPMA, y ste actualmente en su versin 3 cuenta con 3 categorias de competencias (Comportamiento, Contextuales y Tcnicas) de las cuales se desprenden 3 exmenes especializados en el estudio de dichas categoras",
      image: "assets/img/examenes_categorizados.png",// 
    }
    ,
    {
      title: "Resalto de Respuesta Correcta",
      description: "Cuando respondas correctamente, se resaltar tu respuesta con color verde, asmismo cuando respondas incorrectamente, se resaltar tambin tu respuesta incorrecta con color rojo",
      image: "assets/img/resalto_correcta.png",//
    }
    ,
    {
      title: "Resumen de Respuestas Correctas e Incorrectas",
      description: "Luego de responder el exmen podrs visualizar el resumen de tus respuestas y tocar cada pregunta resaltada podrs visualizar la respuesta correcta y el argumento en el que se basa",
      image: "assets/img/resumen_respuestas.png",// 
    }
    ,
    {
      title: "Seguimiento de Resultados",
      description: "Siempre tendrs un registro de tu progreso por cada exmen que realices",
      image: "assets/img/progreso.png",// 
    }
  ];

  


  constructor(public navCtrl: NavController, public navParams: NavParams) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad TutorialPage');
  }

  goToMainPage(){
    // this.navCtrl.push(MyApp);
    this.navCtrl.setRoot(WelcomePage);
  }

}



